import React, { Component, lazy, Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import Loader from '../../components/UI/Loader/Loader';
import ErrorBoundary from '../../HOCs/ErrorBoundary';
import Layout from '../Layout/Layout';
import { suspenseNode, suspenseNode2 } from '../../HOCs/suspensed';
// import CssBaseline from '@material-ui/core/CssBaseline';
// import Checkout from '../Checkout/Checkout';

const Checkout = lazy(() =>
  import(/* webpackChunkName: "Checkout" */ '../Checkout/Checkout')
);

const BurgerBuilder = lazy(() =>
  import(/* webpackChunkName: "BurgerBuilder" */ '../BurgerBuilder/BurgerBuilder')
);

const Orders = lazy(() =>
  import(/* webpackChunkName: "Orders" */ '../Orders/Orders')
);

const SBurgerBuilder = suspenseNode2(BurgerBuilder);
const SOrders = suspenseNode2(Orders);
const SCheckout = suspenseNode2(Checkout);

class App extends Component {
  public render() {
    return (
      <>
        {/* <CssBaseline /> */}
        <Layout>
          <ErrorBoundary>
            <Suspense fallback={<Loader />}>
              <Switch>
                <Route path="/" exact={true} component={SBurgerBuilder} />
                <Route path="/all-orders" exact={true} component={SOrders} />
                <Route path="/checkout" exact={false} component={SCheckout} />
              </Switch>
            </Suspense>
            {/* {SNCheckout} */}
          </ErrorBoundary>
        </Layout>
      </>
    );
  }
}

export default App;
